import React, {Component} from 'react';
const Tshirt       = require('./img/tshirt.svg')
const Underwear    = require('./img/underwear.svg')
const Socks        = require('./img/socks.svg')
const Soap         = require('./img/soap.svg')
const Razor        = require('./img/razor.svg')
const Paper_towel  = require('./img/paper_towel.svg')
const Toilet_paper = require('./img/toilet_paper.svg')
const Batteries    = require('./img/batteries.svg')

class CreateBox extends Component {
    constructor( props ) {
        super( props );
        this.state = {...this.props.store.state,
            allProducts: [
                Tshirt,
                Underwear,
                Socks,
                Razor,
                Toilet_paper,
                Paper_towel,
                Soap,
                Batteries
            ],
            showModal: false
        }
    }
    
    render() {
        const allProducts = this.state.allProducts
        let showModal = this.state.showModal
        let count = 0
        const handleClickIcon = () => {
            this.setState({...this.state, showModal: !this.state.showModal})
            showModal = this.state.showModal
        }

        const handleSubmit = (e) => {
            e.preventDefault()
            this.props.store.state = {...this.state}
        }

        const Modal = () => {
            return <div className="modal">
                <div className="modal-content">
                    <span className="close" onClick={handleClickIcon}>&times;</span>
                    <div className="modal-icon"><img src={allProducts[0]} /></div>
                    <form>
                        <div>
                            <label>Color:</label>
                            <select>
                                <option value="white">White</option>
                                <option value="black">Black</option>
                                <option value="red">Red</option>
                                <option value="blue">Blue</option>
                                <option value="green">Green</option>
                            </select>
                        </div>
                        <div>
                            <label>Size:</label>
                            <select>
                                <option value="small">SM</option>
                                <option value="medium">M</option>
                                <option value="large">LG</option>
                                <option value="extra-large">XL</option>
                                <option value="extra-extra-large">XXL</option>
                            </select>
                        </div>
                        <div>
                            <label>Type:</label>
                            <select>
                                <option value="cotton">100% Cotton</option>
                                <option value="mix">50% Cotton / 50% Polyester</option>
                                <option value="hemp">Hemp</option>
                            </select>
                        </div>
                        <div>
                            <label>Quantity:</label>
                            <input type="number"/>
                        </div>
                        <button type="submit">Submit</button>
                    </form>
                </div>
            </div>
        }
        return (
            <section className="CreateBox">
                <p>Customize Your Order</p>
                <div className="product-icons">
                    {
                        allProducts.map(( product ) => {
                            return (
                                <div className="icon" key={count++}>
                                    <img onClick={handleClickIcon} src={product} />
                                </div>
                            )
                        })
                    }
                </div>
                { showModal ? <Modal/>: ''}
            </section>
        );
    }
}

export default CreateBox;