import React, { Component } from 'react'
import CreateBox from './createBox'

class Start extends Component {
    constructor(props) {
        super(props);
        this.state = {...this.props.store.state}
    }

    render() { 
        const show = this.state.showSection
        console.log(show)
        const handleChange = (e) => {        
            this.state = {...this.state,
                Email: e.target.value
            }
        }
    
        const handleSubmit = (e) => {
            e.preventDefault()
            this.props.store.state = {...this.state}
            this.setState({ showSection: !this.state.showSection })
            
        }

        return (show ?
                <section className="Start">
                    <p>Choose Your Subscription</p>
                    <p className="box"><i className="fal fa-box-check"></i></p>
                    <form onSubmit={handleSubmit}>
                        <div>
                            <label>Email:</label>
                            <input type="email" onChange={handleChange}/>
                        </div>
                        <div>
                            <button type="submit">Join</button>
                        </div>
                    </form>
                </section>
                :
                <CreateBox store={this.state}/>
        );
    }
}

export default Start;