import React, { Component } from 'react';
import * as AllViews from './views'
import Logo from './views/img/logo.svg'
import './style.css'

function Store(initialState = {
    allViews: AllViews.display,
    Email: 'Hi',
    Fname: '',
    Lname: '',
    Gender: '',
    Order: {},
    showSection: 'Start'
}) {
    this.state = initialState;
}


Store.prototype.mergeState = function(partialState) {
    Object.assign(this.state, partialState);
};

class Main extends Component {
    constructor(props) {
        super(props)
       this.myStore = new Store();
    }

    render() {
        const display = this.myStore.state.allViews
        const show = this.myStore.state.showSection
        return (
            <main>
                <div className="logo">
                    <img src={Logo}/>
                </div>
                {
                    Object.keys( display ).map(
                        ( View ) => { 
                            let Display = display[ View ] === undefined ? false : display[ View ]
                            console.log(View)
                            if ( View == show ) {
                                return <Display store={this.myStore} key={ View }></Display> 
                            }
                        }
                    )
                }
            </main>
        );
    }
}
 
export default Main;